.name{
    font-size: 20px;
    font-weight: 600;
}
.nav-container{
    border-bottom: .5px rgb(178, 176, 176) solid;
    
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.menuItem{
    cursor: pointer;
    font-size: 14px;
}
.name{
    cursor: pointer;
}
.menuItem:hover{
    text-decoration: underline;
}
.menu{
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: .5cm;
}
.log-out{
    width: .6cm;
}
.left-nav{
    display: flex;
    column-gap: .1cm;
    align-items: center;
}
.logo{
    width: 1cm;
}

@media screen and (max-width:600px){
    .name{
        display: none;
    }
}