.Files {
    padding: 10vh 0vw;
}

.user-name {
    font-size: 30px;
    font-weight: 600;
}

.nf-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.rxn-no-img {
    width: 5.5cm;
    padding-top: 5vh;
}

.save-file {
    font-size: 14px;
    padding: .17cm .5cm;
    cursor: pointer;


}

.sub-search {
    color: rgb(96, 95, 95);
}

.saved-line {
    padding-top: 2vh;
    font-size: 23px;
    font-weight: 600;
}

.file-name {
    font-size: 18px;
    cursor: pointer;
    padding-top: .1cm;
    width: fit-content;
}

.file-name:hover {
    text-decoration: underline;
}

@media screen and (max-width:600px) {
    .rxn-no-img {
        width: 4.5cm;
    }

    .user-name {
        font-size: 25px;
    }

    .saved-line {

        font-size: 18px;

    }

    .sub-search {
        font-size: 14px;
    }

    .file-name {
        font-size: 17px;

    }
}

@media screen and (max-width:500px) {
    .rxn-no-img {
        width: 4cm;
    }

    .user-name {
        font-size: 20px;
    }

    .file-name {
        font-size: 17px;

    }
}

@media screen and (max-width:400px) {
    .rxn-no-img {
        width: 3.5cm;
    }
}

@media screen and (max-width:300px) {
    .rxn-no-img {
        width: 3cm;
    }
}