.folders{
    width: 1.7cm;
}
.Home{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 90vh;
    // border: 1px solid black;
}

.login-with-google-btn {
    // transition: background-color .3s, box-shadow .3s;
    cursor: pointer;
    padding: 12px 16px 12px 12px;
    border: none;
    border-radius: 3px;
    
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 3px, rgba(0, 0, 0, 0.23) 0px 1px 3px;


    color: #313030;
    font-size: 13px;
    font-weight: 500;
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Fira Sans","Droid Sans","Helvetica Neue",sans-serif;
    
  
    
    display: flex;
    justify-content: center;
    align-items: center;
    
   
  }
  .login-with-google-btn:hover{
    background-color: rgb(247, 245, 245);
  }
.tagline{
    font-size: 17px;
    font-weight: 600;
}
.sub-tagline{
    color: rgb(66, 66, 66);
    font-size: 16px;

}
.form-section{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: .5cm;
}
input::file-selector-button {
    font-weight: 600;
    color: rgb(23, 24, 24);
    padding: 0.5em;
    border: none;
    background-color: #a3f1a3;
    cursor: pointer;
   
    
    border-radius: 3px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 3px, rgba(0, 0, 0, 0.23) 0px 1px 3px;

}
input::file-selector-button:hover{
    background-color: #85ea85;

}
.submit{
    cursor: pointer;
    border: none;
    background-color: #99d4fe;
    font-size: 15px;
    padding: .175cm .5cm;
    width: 100%;
    border-radius: 3px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 3px, rgba(0, 0, 0, 0.23) 0px 1px 3px;

}
.submit:hover{
    background-color: #92c9f0;
}
.welcome{
    // border: 1px solid black;
    width: fit-content;
    padding: .5cm;
    display: flex;
    flex-direction: column;
    align-items: center;
    // row-gap: .3cm;
    background-color: rgb(249, 247, 247);
    justify-content: center;
    // box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 3px, rgba(0, 0, 0, 0.23) 0px 1px 3px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    
   
}

@media screen and (max-width:800px){
    .folders{
        width: 1.4cm;
    }
    .tagline{
        font-size: 16px;
    }
    .sub-tagline{
        font-size: 14px;
    }
}
@media screen and (max-width:500px)
{
    .login-with-google-btn{
        font-size: 14px;
    }
   
}
@media screen and (max-width:500px)
{
    .login-with-google-btn{
        font-size: 13px;
    }
}